<template>
  <div class="section">
    <el-table :data="list" border style="width: 100%">
      <el-table-column prop="status" label="状态" />
      <el-table-column prop="well_no" label="井号" />
      <el-table-column prop="area" label="区块" />
      <el-table-column prop="oilfield" label="公司" />
      <el-table-column prop="operate_unit" label="施工单位" />
      <el-table-column prop="operate_team" label="井队" />
      <el-table-column prop="design_start_at" label="开钻时间" />
      <el-table-column prop="design_depth" label="设计井深(m)" />
      <el-table-column prop="current_depth" label="当前井深(m)" />
      <el-table-column prop="footage" label="日进尺(m)" />
      <el-table-column prop="current_layer" label="钻达层位" />
      <el-table-column prop="description" label="施工简况" />
      <el-table-column prop="density" label="密度(g/cm³)" />
      <el-table-column prop="viscosity" label="粘度(s)" />
      <el-table-column prop="drilling_monitor" label="监督" />
      <el-table-column prop="well_control" label="井控类别" />
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page=current
            :page-sizes="[5, 10, 15, 20]"
            :page-size=size
            layout="total, sizes, prev, pager, next, jumper"
            :total=total>
          </el-pagination>
  </div>
</template>

<script>
import request from '@/utils/request'
export default {
  props: ["route"],
  data () {
     return {
        list:[],
        size: 10,
        current: 1,
        total: 0
      }
  },

  components: {},

  computed: {
    
  },

  mounted: function() {
    this.getData()
  },

  methods: {
    handleSizeChange(val) {
      this.size = val;
      this.getData()
    },
    handleCurrentChange(val) {
      this.current = val;
      this.getData()
    },
    getData() {
      let params = this.route;
      request({
        url: '/report/index',
        method: 'get',
        params: {
          ...params,
          page_num: this.current,
          page_size: this.size
        }
      }).then(res => {
        this.list = res.well_data;
        this.total = res.count
      })
    }
  }
}
</script>


